<template>
  <preview-wrapper :pageID="pageID" @closePreview="closePreview">
    <p class="page-title paddingL30">页面设置</p>
    <div class="preview-info-wrapper">
      <div class="page-info">
        <div class="page-cover">
          <image-cropper
            :pageID="pageID"
            :url="pageData.coverImage"
            @updata-url="updataURL"
          />
        </div>
        <div class="page-cover"></div>
      </div>
    </div>
  </preview-wrapper>
</template>
<script>
import PreviewWrapper from "@/components/preview-wrapper.vue";
import imageCropper from "@/components/image-cropper.vue";

export default {
  props: {
    pageID: {
      type: String,
      require: true,
    },
    pageData: {
      type: Object,
      require: true,
    },
  },
  components: {
    PreviewWrapper,
    imageCropper,
  },
  methods: {
    closePreview() {
      this.$emit("closePreview", false);
    },
    updataURL(url) {
      this.$emit("updata-url", url);
    },
  },
};
</script>
<style lang="less" scoped>
.preview-info-wrapper {
  padding: 30px 30px 60px;
}
.page-info {
  display: flex;
  .page-cover {
    width: 120px;
    height: 120px;
  }
}
</style>